import React, { useState } from 'react';
import { StatusBar } from 'expo-status-bar';
import axios from 'axios';
import {
  StyleSheet,
  Text,
  View, 
  TextInput,
  TouchableOpacity,
  ScrollView,
} from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';

export default function SignupPage({ navigation }) {
  // User info
  const [name, setName] = useState('');
  const [surname, setSurname] = useState('');
  const [address, setAddress] = useState('');
  const [phone, setPhone] = useState('');
  const [password, setPassword] = useState('');

  // Next of Kin 1
  const [nok1Name, setNok1Name] = useState('');
  const [nok1Surname, setNok1Surname] = useState('');
  const [nok1Id, setNok1Id] = useState('');
  const [nok1Phone, setNok1Phone] = useState('');

  const handleSignUp = async () => {
    const payload = {
      name,
      surname,
      address,
      phone,
      password,
      nextOfKin1: {
        name: nok1Name,
        surname: nok1Surname,
        id: nok1Id,
        phone: nok1Phone,
      }
    };

    try {
      const { data } = await axios.post(
        'https://agilitysystemmobileapp.onrender.com/api/signup',
        payload,
        { headers: { 'Content-Type': 'application/json' } }
      );

      alert('User registered successfully!');
      navigation.navigate('LandingPage');
    } catch (error) {
      if (error.response) {
        alert('Error: ' + error.response.data.message);
      } else {
        alert('Network error. Try again.');
      }
      console.error(error);
    }
  };

  return (
    <LinearGradient colors={['#6b46c1', '#f6e05e']} style={styles.background}>
      <ScrollView contentContainerStyle={styles.scroll}>
        <View style={styles.card}>
          <Text style={styles.title}>Agility Finance</Text>
          <Text style={styles.subtitle}>Sign Up</Text>

          {/* User Info */}
          <TextInput style={styles.input} placeholder="Name" value={name} onChangeText={setName} />
          <TextInput style={styles.input} placeholder="Surname" value={surname} onChangeText={setSurname} />
          <TextInput style={styles.input} placeholder="Address" value={address} onChangeText={setAddress} />
          <TextInput style={styles.input} placeholder="Phone Number" keyboardType="phone-pad" value={phone} onChangeText={setPhone} />
          <TextInput style={styles.input} placeholder="Password" secureTextEntry value={password} onChangeText={setPassword} />

          {/* Next of Kin 1 */}
          <Text style={styles.sectionTitle}>Next of Kin</Text>
          <TextInput style={styles.input} placeholder="Name" value={nok1Name} onChangeText={setNok1Name} />
          <TextInput style={styles.input} placeholder="Surname" value={nok1Surname} onChangeText={setNok1Surname} />
          <TextInput style={styles.input} placeholder="ID Number" value={nok1Id} onChangeText={setNok1Id} />
          <TextInput style={styles.input} placeholder="Phone Number" keyboardType="phone-pad" value={nok1Phone} onChangeText={setNok1Phone} />

          <TouchableOpacity style={styles.button} onPress={handleSignUp} activeOpacity={0.8}>
            <LinearGradient colors={['#f6e05e', '#6b46c1']} start={{ x: 0, y: 0 }} end={{ x: 1, y: 1 }} style={styles.buttonGradient}>
              <Text style={styles.buttonText}>Sign Up</Text>
            </LinearGradient>
          </TouchableOpacity>
        </View>
        <StatusBar style="light" />
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  background: { flex: 1 },
  scroll: { flexGrow: 1, justifyContent: 'center', alignItems: 'center', paddingVertical: 20 },
  card: { width: '90%', backgroundColor: 'rgba(255,255,255,0.9)', borderRadius: 20, padding: 25, shadowColor: '#000', shadowOffset: { width: 0, height: 6 }, shadowOpacity: 0.2, shadowRadius: 10, elevation: 10 },
  title: { fontSize: 32, fontWeight: '800', color: '#6b46c1', textAlign: 'center' },
  subtitle: { fontSize: 16, color: '#555', textAlign: 'center', marginBottom: 20 },
  sectionTitle: { fontSize: 18, fontWeight: '700', color: '#6b46c1', marginTop: 20, marginBottom: 10 },
  input: { height: 50, borderColor: '#6b46c1', borderWidth: 1, borderRadius: 12, paddingHorizontal: 15, marginBottom: 15, backgroundColor: '#fff' },
  button: { width: '100%', borderRadius: 12, overflow: 'hidden', marginTop: 15, marginBottom: 10 },
  buttonGradient: { paddingVertical: 15, alignItems: 'center', justifyContent: 'center' },
  buttonText: { color: '#fff', fontSize: 18, fontWeight: 'bold' },
});
