import React, { useState } from 'react';
import {
  Modal,
  View,
  Text,
  TouchableOpacity,
  StyleSheet,
  TextInput,
  ScrollView,
  Image,
  Platform,
  Alert,
} from 'react-native';
import axios from 'axios';
import * as ImagePicker from 'expo-image-picker';

const ApplyLoanModal = ({ visible, onClose }) => {
  const [fullName, setFullName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [amount, setAmount] = useState('');
  const [purpose, setPurpose] = useState('');
  const [idImage, setIdImage] = useState(null);
  const [selfieImage, setSelfieImage] = useState(null);

  // Pick ID from gallery
  const handlePickImage = async () => {
    const { status } = await ImagePicker.requestMediaLibraryPermissionsAsync();
    if (status !== 'granted') {
      Alert.alert('Permission denied', 'Cannot access gallery');
      return;
    }

    const result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.Images,
      quality: 0.7,
    });

    if (!result.canceled && result.assets.length > 0) {
      setIdImage(result.assets[0]);
    }
  };

  // Take selfie with camera
  const handleTakeSelfie = async () => {
    const { status } = await ImagePicker.requestCameraPermissionsAsync();
    if (status !== 'granted') {
      Alert.alert('Permission denied', 'Cannot use camera');
      return;
    }

    const result = await ImagePicker.launchCameraAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.Images,
      quality: 0.7,
    });

    if (!result.canceled && result.assets.length > 0) {
      setSelfieImage(result.assets[0]);
    }
  };

  const handleSubmit = async () => {
    const formData = new FormData();
    formData.append('fullName', fullName);
    formData.append('email', email);
    formData.append('phone', phone);
    formData.append('amount', amount);
    formData.append('purpose', purpose);

    if (idImage) {
      formData.append('idImage', {
        uri:
          Platform.OS === 'android'
            ? idImage.uri
            : idImage.uri.replace('file://', ''),
        type: 'image/jpeg',
        name: 'id.jpg',
      });
    }

    if (selfieImage) {
      formData.append('selfieImage', {
        uri:
          Platform.OS === 'android' || Platform.OS === 'iphone'
            ? selfieImage.uri
            : selfieImage.uri.replace('file://', ''),
        type: 'image/jpeg',
        name: 'selfie.jpg',
      });
    }

    try {
      await axios.post('https://agilitysystemmobileapp.onrender.com/api/loans', formData, {
        headers: { 'Content-Type': 'multipart/form-data' },
      });
      Alert.alert('Success', 'Loan submitted successfully!');
      onClose();
    } catch (error) {
      console.error('Error submitting loan:', error);
      Alert.alert('Error', 'Failed to submit loan application.');
    }
  };

  return (
    <Modal transparent visible={visible} animationType="slide">
      <View style={styles.overlay}>
        <View style={styles.content}>
          <Text style={styles.title}>Apply for a Loan</Text>
          <ScrollView>
            <TouchableOpacity style={styles.uploadBtn} onPress={handlePickImage}>
              <Text style={styles.uploadText}>
                {idImage ? 'Change ID Image' : 'Upload ID Image'}
              </Text>
            </TouchableOpacity>
            {idImage && (
              <Image source={{ uri: idImage.uri }} style={styles.preview} />
            )}

            <TouchableOpacity style={styles.selfieBtn} onPress={handleTakeSelfie}>
              <Text style={styles.uploadText}>
                {selfieImage ? 'Retake Selfie' : 'Take Selfie'}
              </Text>
            </TouchableOpacity>
            {selfieImage && (
              <Image source={{ uri: selfieImage.uri }} style={styles.preview} />
            )}

            <TextInput
              style={styles.input}
              placeholder="Full Name"
              value={fullName}
              onChangeText={setFullName}
            />
            <TextInput
              style={styles.input}
              placeholder="Email"
              keyboardType="email-address"
              value={email}
              onChangeText={setEmail}
            />
            <TextInput
              style={styles.input}
              placeholder="Phone Number"
              keyboardType="phone-pad"
              value={phone}
              onChangeText={setPhone}
            />
            <TextInput
              style={styles.input}
              placeholder="Loan Amount"
              keyboardType="numeric"
              value={amount}
              onChangeText={setAmount}
            />
            <TextInput
              style={[styles.input, { height: 80 }]}
              placeholder="Purpose of Loan"
              value={purpose}
              onChangeText={setPurpose}
              multiline
            />

            <TouchableOpacity style={styles.submitBtn} onPress={handleSubmit}>
              <Text style={styles.submitText}>Submit Application</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.closeBtn} onPress={onClose}>
              <Text style={styles.closeText}>Close</Text>
            </TouchableOpacity>
          </ScrollView>
        </View>
      </View>
    </Modal>
  );
};

const styles = StyleSheet.create({
  overlay: {
    flex: 1,
    backgroundColor: 'rgba(0,0,0,0.5)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  content: {
    width: '90%',
    backgroundColor: '#fff',
    borderRadius: 15,
    padding: 20,
    maxHeight: '80%',
  },
  title: {
    fontSize: 18,
    fontWeight: '700',
    marginBottom: 15,
    textAlign: 'center',
  },
  input: {
    borderWidth: 1,
    borderColor: '#ccc',
    padding: 10,
    borderRadius: 8,
    marginBottom: 10,
  },
  submitBtn: {
    backgroundColor: '#6A0DAD',
    padding: 12,
    borderRadius: 8,
    marginTop: 10,
  },
  submitText: {
    color: '#fff',
    textAlign: 'center',
    fontWeight: '600',
  },
  closeBtn: {
    marginTop: 10,
    backgroundColor: '#999',
    padding: 10,
    borderRadius: 8,
  },
  closeText: {
    color: '#fff',
    textAlign: 'center',
  },
  uploadBtn: {
    backgroundColor: '#FFA500',
    padding: 12,
    borderRadius: 8,
    marginBottom: 10,
  },
  selfieBtn: {
    backgroundColor: '#00A6FF',
    padding: 12,
    borderRadius: 8,
    marginBottom: 10,
  },
  uploadText: {
    color: '#fff',
    textAlign: 'center',
    fontWeight: '600',
  },
  preview: {
    width: 100,
    height: 100,
    marginVertical: 10,
    borderRadius: 8,
    alignSelf: 'center',
  },
});

export default ApplyLoanModal;
