import React, { useState } from 'react';
import { View, Text, TouchableOpacity, StyleSheet, ScrollView } from 'react-native';
import Icon from 'react-native-vector-icons/MaterialCommunityIcons';
import ApplyLoanModal from '../MainmenuModals/ApplyLoan.js';
import LoanStatusModal from '../MainmenuModals/LoanStatusModal.js';
import LoanHistoryModal from '../MainmenuModals/LoanHistroryModal.js';
import MakeRepaymentModal from '../MainmenuModals/MakeRepaymentModal.js';
import RepaymentHistoryModal from '../MainmenuModals/RepaymentHistoryModal.js';
import OverdueAlertsModal from '../MainmenuModals/OverdueAlertsModal.js';
import ProfileModal from '../MainmenuModals/ProfileModal.js';

const MainMenu = ({ navigation }) => {

  const [applyLoanVisible, setApplyLoanVisible] = useState(false);

  const [loanStatusVisible, setLoanStatusVisible] = useState(false);
  const [loanHistoryVisible, setLoanHistoryVisible] = useState(false);
  const [makeRepaymentVisible, setMakeRepaymentVisible] = useState(false);
  const [repaymentHistoryVisible, setRepaymentHistoryVisible] = useState(false);
  const [overdueAlertsVisible, setOverdueAlertsVisible] = useState(false);
  const [profileVisible, setProfileVisible] = useState(false);



  return (
    <View style={styles.container}>
      {/* Wallet / Header */}
      <View style={styles.walletCard}>
        <Text style={styles.walletTitle}>Loan Wallet</Text>
        <Text style={styles.balance}>USD 0.00</Text>
      </View>

      {/* Menu Grid */}
      <ScrollView contentContainerStyle={styles.menuGrid}>
        <MenuItem icon="file-document-edit" label="Apply Loan" onPress={() => setApplyLoanVisible(true)} />
        <MenuItem icon="eye" label="Loan Status" onPress={() => setLoanStatusVisible(true)} />
        <MenuItem icon="history" label="Loan History" onPress={() => setLoanHistoryVisible(true)} />
        <MenuItem icon="credit-card-outline" label="Make Repayment" onPress={() => setMakeRepaymentVisible(true)} />
        <MenuItem icon="clipboard-text-outline" label="Repayment History" onPress={() => setRepaymentHistoryVisible(true)} />
        <MenuItem icon="alert-outline" label="Overdue Alerts" onPress={() => setOverdueAlertsVisible(true)} />
        <MenuItem icon="account-edit" label="Profile" onPress={() => setProfileVisible(true)} />
      </ScrollView>

      <ApplyLoanModal visible={applyLoanVisible} onClose={() => setApplyLoanVisible(false)} />
      <LoanStatusModal visible={loanStatusVisible} onClose={() => setLoanStatusVisible(false)} />
      <LoanHistoryModal visible={loanHistoryVisible} onClose={() => setLoanHistoryVisible(false)} />
      <MakeRepaymentModal visible={makeRepaymentVisible} onClose={() => setMakeRepaymentVisible(false)} />
      <RepaymentHistoryModal visible={repaymentHistoryVisible} onClose={() => setRepaymentHistoryVisible(false)} />
      <OverdueAlertsModal visible={overdueAlertsVisible} onClose={() => setOverdueAlertsVisible(false)} />
      <ProfileModal visible={profileVisible} onClose={() => setProfileVisible(false)} />

    </View>
  );
};

const MenuItem = ({ icon, label, onPress }) => (
  <TouchableOpacity style={styles.menuItem} onPress={onPress}>
    <Icon name={icon} size={28} color="#6A0DAD" />
    <Text style={styles.menuText}>{label}</Text>
  </TouchableOpacity>
);

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#F9F9F9',
  },
  walletCard: {
    backgroundColor: '#6A0DAD',
    padding: 20,
    borderBottomLeftRadius: 20,
    borderBottomRightRadius: 20,
  },
  walletTitle: {
    fontSize: 18,
    color: '#FFD700',
    fontWeight: '600',
  },
  balance: {
    fontSize: 24,
    color: '#FFFFFF',
    fontWeight: '700',
    marginTop: 5,
  },
  menuGrid: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'space-between',
    padding: 15,
  },
  menuItem: {
    width: '47%',
    backgroundColor: '#FFFFFF',
    borderRadius: 15,
    paddingVertical: 20,
    paddingHorizontal: 10,
    marginBottom: 15,
    alignItems: 'center',
    shadowColor: '#000',
    shadowOpacity: 0.05,
    shadowRadius: 5,
    elevation: 3,
  },
  menuText: {
    marginTop: 8,
    fontSize: 14,
    textAlign: 'center',
    color: '#333333',
    fontWeight: '500',
  },
});

export default MainMenu;

